import {React, useEffect} from 'react';
import Navbar from './Navbar'
import logo from '../Media/logo.png'
import { Typography, Stack, Button } from '@mui/material';
import responsiveDesign from './responsiveDesign.css';
import DrawerEl from './DrawerEl'

const Opportunities = () => {
    return ( <div>
<div style={{width:'200px', marginLeft:'auto', marginRight:'auto'}}>
<img style={{width:'200px'}} src={logo} id="logo"></img></div>
       <div id="navbar"> <Navbar></Navbar></div><div id="drawerElement"><DrawerEl></DrawerEl></div>
        <div id="invisibleMobile" style={{width:'75%', position: 'relative', marginRight:'auto', marginLeft:'auto'}}>
       <Typography variant="h3" sx={{fontFamily: "'Roboto Mono', monospace", position: 'relative', marginLeft:'25%', '@media(max-width: 480px)' : {visibility: 'hidden'}}} id="invisible">Pleitosti v Pfuku</Typography>
       <Typography  variant="h5" sx={{fontFamily: "'Roboto Mono', monospace", paddingTop: '25px', '@media(max-width: 480px)' : {visibility: 'hidden'}}}>Jak monosti nabz spolek Pfuk a jak je vyut?</Typography>
       <Stack direction="row">
        <div style={{width:'50%', paddingRight: '15px', textAlign: 'justify'}}><Typography variant="h6" sx={{fontFamily: "'Roboto Mono', monospace", paddingTop: '25px', '@media(max-width: 480px)' : {visibility: 'hidden'}}}> Aktivn zapojen</Typography>
        <Typography variant="p" sx={{fontFamily: "'Roboto Mono', monospace", '@media(max-width: 480px)' : {visibility: 'hidden'} }}>
        Mte vlastn npad? Chtli byste zorganizovat studentskou akci vtho nebo menho rozmru? Chyb vm nco v naem kampusu?
        Chtli byste nco odpednet? Mte obecn njakou vlastn iniciativu, kterou byste chtli zrealizovat, ale nevte jak?
        Napite nm co chcete dlat a my Vm pomeme. Seeneme vm finance, pomeme sehnat prostory, vydme povolen od fakulty a vechny 
        dleit vci, bez kterch se nic organizovat ned. Lid ze spolku vm tak pomou a kdy vechno dopadne, tak spolen
        s Pfukckm zattnm me vzniknout nco velkho a krsnho. 

        </Typography>
        </div>
        <div style={{width:'50%', paddingRight: '15px', textAlign: 'justify'}}>
        <Typography variant="h6" sx={{fontFamily: "'Roboto Mono', monospace", paddingTop: '25px', '@media(max-width: 480px)' : {visibility: 'hidden'}}}>Skoro-aktivn zapojen</Typography>
        <Typography variant="p" sx={{fontFamily: "'Roboto Mono', monospace", '@media(max-width: 480px)' : {visibility: 'hidden'}}}>
        Nemte konkrtn iniciativu, ale i pes to byste se rdi pipojili do spolkov organizace a pomohli s projekty, kter spolek
        zatiuje? Pidejte se! Nikdy nen dost rukou a my budeme rdi, kdy se pipojte. Najdte si projekt, kter vs bude bavit 
        a nabdnte sv ruce. Mete pomoct njakou konkrtn schopnost, kterou jste nabyli v prbhu vaeho studia a nebo zcela obecn
        teba pi stavn, za barem, a td.

        </Typography></div>
       </Stack>
       <Button href="/ideas" variant="contained" sx={{position: 'relative', marginRight: 'auto', 
        width: '100%', zIndex: 2, backgroundColor: 'green', marginTop: '40px', textAlign: 'center',
        borderRadius: '25px', height: '60px', borderStyle: 'solid'}}>Mm skvl npad na skvlou akci!</Button>
       </div>
       <div id="invisibleWide" style={{padding: '25px', textAlign: 'justify'}}>
       <h2> Pleitosti</h2>
        <h4>Aktivn zapojen</h4>
        <p> Mte vlastn npad? Chtli byste zorganizovat studentskou akci vtho nebo menho rozmru? Chyb vm nco v naem kampusu?
        Chtli byste nco odpednet? Mte obecn njakou vlastn iniciativu, kterou byste chtli zrealizovat, ale nevte jak?
        Napite nm co chcete dlat a my Vm pomeme. Seeneme vm finance, pomeme sehnat prostory, vydme povolen od fakulty a vechny 
        dleit vci, bez kterch se nic organizovat ned. Lid ze spolku vm tak pomou a kdy vechno dopadne, tak spolen
        s Pfukckm zattnm me vzniknout nco velkho a krsnho. </p>
        <h4>polo-Aktivn zapojen</h4>
        <p>   Nemte konkrtn iniciativu, ale i pes to byste se rdi pipojili do spolkov organizace a pomohli s projekty, kter spolek
        zatiuje? Pidejte se! Nikdy nen dost rukou a my budeme rdi, kdy se pipojte. Najdte si projekt, kter vs bude bavit 
        a nabdnte sv ruce. Mete pomoct njakou konkrtn schopnost, kterou jste nabyli v prbhu vaeho studia a nebo zcela obecn
        teba pi stavn, za barem, a td.</p>
        <Button href="/ideas" variant="contained" sx={{position: 'relative', marginRight: 'auto', 
        width: '100%', zIndex: 2, backgroundColor: 'green', marginTop: '40px', textAlign: 'center',
        borderRadius: '25px', height: '60px', borderStyle: 'solid'}}>Mm skvl npad na skvlou akci!</Button>
       </div>
    </div> );
}
 
export default Opportunities;